<template>
    <div class="cityWrap">
        <header>
            <router-link to="/home">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <div>{{cityInfo ? cityInfo.name : ''}}</div>
            <router-link to="/home">切换城市</router-link>
        </header>
        <div class="form">
            <div>
                <input type="text" placeholder="输入学校、商务楼、地址" v-model="inputV">
            </div>
            <div>
                <div class="searchBtn" @click="searchPlace">提交</div>
            </div>
        </div>
        <div class="history" v-if="isShowHistoryHeader">搜索历史</div>
        <!--将来展示搜索内容&&历史纪录-->
        <ul>
            <li v-for="p in placeArr" :key="p.geohash" @click="choosePlace(p)">
                <h4 class="ellipsis p_name" >{{p.name}}</h4>
                <p class="ellipsis p_address">{{p.address}}</p>
            </li>
        </ul>
        <div class="clearHistory" v-if="isShowClearBtn" @click="clearAllPlace">清空所有</div>
    </div>
</template>

<script>
    export default {
        name: "City",
        data(){
            return {
                cityId:0,
                cityInfo:null,
                inputV:"",
                placeArr:[],
                // 控制搜索历史的显隐
                isShowHistoryHeader:true,
                // 控制清除历史记录按钮的显隐
                isShowClearBtn:false
            }
        },
        created(){
            // 获取params传递来的城市id, 根据id发起网络请求获取城市信息
            this.cityId = this.$route.params.cityId;
            this.axios.get("/v1/cities/"+this.cityId).then(res=>{
                this.cityInfo = res;
            }).catch(err=>{});
            // 进入该页面时, 查看是否有历史记录, 如果有, 展示
            if (localStorage.getItem("placeHistory")) {
                this.placeArr = JSON.parse(localStorage.getItem("placeHistory"));
                // 显示清除按钮
                this.isShowClearBtn = true;
            }
        },
        methods: {
            searchPlace(){
                this.axios.get("/v1/pois", {
                    params:{
                        city_id:this.cityInfo.id,
                        keyword:this.inputV,
                        type:"search"
                    }
                }).then(res=>{
                    console.log(res);
                    this.placeArr = res;
                    this.isShowHistoryHeader = false;
                }).catch(err=>{
                    console.log(err);
                });
            },
            choosePlace(p) {
                /*
                    第一次存, 先创建一个数组, 将p放进数组, 将数组存储localStorage
                    以后存, 获取数组, 将p放进数组, 将数组存储localStorage
                 */
                // console.log(localStorage.getItem("placeHistory"));
                if (!localStorage.getItem("placeHistory")) {
                    // 第一次存
                    let hisArr = [];
                    hisArr.push(p);
                    localStorage.setItem("placeHistory", JSON.stringify(hisArr));
                }else {
                    // 之后存
                    let hisArr = JSON.parse(localStorage.getItem("placeHistory"));
                    // 去重逻辑
                    hisArr.push(p);
                    localStorage.setItem("placeHistory", JSON.stringify(hisArr));
                }
                this.$router.push("/food");
                localStorage.setItem("msite",JSON.stringify(p));
            },
            clearAllPlace(){
                // 点击清除所有， 删除placeHistory的localStorage， 清空下拉列表, 隐藏清空按钮
                localStorage.removeItem("placeHistory");
                this.placeArr = [];
                this.isShowClearBtn = false;
            },

        }
    }
</script>

<style scoped>
    .cityWrap {
        padding-top: 2.35rem;
    }
    header{
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    header>div{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
        font-size: .8rem;
        font-weight: 700;
    }
    header>a:last-child{
        right: .4rem;
        font-size: .6rem;
        color: #fff;
        position: absolute;
        text-decoration: none;
        top: 50%;
        transform: translateY(-50%);
    }
    header>a:first-child{
        left: .4rem;
        color: #fff;
        position: absolute;
        text-decoration: none;
        top: 50%;
        transform: translateY(-50%);
    }

    .form{
        background-color: #fff;
        padding-top: .4rem;
        border-top: 1px solid #e4e4e4;
        border-bottom: 1px solid #e4e4e4;
    }
    .form>div {
        width: 90%;
        margin: 0 auto;
        text-align: center;
    }
    .form>div:nth-child(1)>input {
        border-radius: .1rem;
        margin-bottom: .4rem;
        width: 100%;
        height: 1.4rem;
        border: 1px solid #e4e4e4;
        padding: 0 .3rem;
        font-size: .65rem;
        color: #333;
        box-sizing: border-box;
        outline: none;
    }
    .searchBtn {
        border-radius: .1rem;
        margin-bottom: .4rem;
        width: 100%;
        height: 1.4rem;
        background-color: #3190e8;
        font-size: .65rem;
        color: #fff;
        outline: none;
        line-height: 1.4rem;
    }
    .history {
        border-top: 1px solid #e4e4e4;
        border-bottom: 1px solid #e4e4e4;
        padding-left: .5rem;
        font: .475rem/.8rem Microsoft YaHei;
    }
    ul{
        background-color: #fff;
    }
    ul li{
        margin: 0 auto;
        padding-top: .65rem;
        border-bottom: 1px solid #e4e4e4;
    }
    .p_name {
        margin: 0 auto .35rem;
        width: 90%;
        font-size: .65rem;
        color: #333;
    }
    .p_address {
        width: 90%;
        margin: 0 auto .55rem;
        font-size: .45rem;
        color: #999;
    }
    .clearHistory{
        font-size: .7rem;
        color: #666;
        text-align: center;
        line-height: 2rem;
        background-color: #fff;
    }
</style>